@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* === helper classes == */
.mark {
  color: $mark-color;
  font-style: inherit;
  font-weight: inherit; }

.text-color-alt {
  color: $base-color; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.float-left {
  float: left; }

.float-right {
  float: right; }

.nobr {
  white-space: nowrap; }

.hide-text {
  @include hide-text; }

.hide {
  display: none !important; }

.desktop-only {
  @include media('<tab-lg') {
    display: none; } }

.devices-only {
  @include media('>=tab-lg') {
    display: none; } }

.mobile-only {
  @include media('>=mob-lg') {
    display: none; } }

/* btn-app */
.btn-app {
  display: inline-block;
  vertical-align: top;
  background-color: black;
  background-position: 2.8rem center;
  background-repeat: no-repeat;
  height: 6.2rem;
  padding: 1rem 4rem 1rem 8rem;
  font-size: 1.8rem;
  line-height: 2.2rem;
  text-align: left;
  text-decoration: none;
  color: white;
  border-radius: 3.1rem;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  // transition
  $prop: background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  &:hover {
    background-color: $highlight-color--hover; } }

.btn-app_google {
  background-image: url(../../img/app-a.png);
  background-size: 34px 42px; }

.btn-app_apple {
  background-image: url(../../img/app-ios.png);
  background-position: 2.8rem 0.8rem;
  background-size: 32px 39px; }

/* btn-app end */

/* === HEADER === */
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  min-width: $layout-min-width;
  height: $header-height;
  z-index: $z-high; }

//  ,     
.header_float {
  position: relative;
  left: auto;
  top: auto; }

.header-holder {
  position: relative;
  height: 100%;
  @include clear; }

.header-frame {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; }

/* logo */
.logo {
  float: left;
  display: block;
  position: relative;
  background-image: url(../../img/logo.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 155px;
  color: #cccddb;
  text-indent: -9999px;
  overflow: hidden;

  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi) {
    background-image: url(../../img/logo-2x.png); }

  @include media('<desk-lg') {
    background-image: url(../../img/logo-sm-2x.png);
    width: 165px;
    margin-top: -13px; }

  @include media('<tab-lg') {
    width: 93px;
    margin-top: 0;
    color: #a7a7ce; }

  &::before {
    content: "";
    display: block;
    @include ratio(155, 33); }

  a, & > span {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%; } }

/* logo end */

/* navigation */
.nav-wrap {
  margin-right: 110px; }

.nav {
  position: relative; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.4rem;
  text-transform: uppercase;
  letter-spacing: 0.01em;
  cursor: default;
  @include clear;

  li {
    position: relative;
    float: left;
    margin-left: 56px; }

  a {
    color: #000;
    text-decoration: none;
    transition: color $animation-speed;

    &:hover {
      color: $highlight-color--hover; } }

  // current
  li.current {
    color: $text-color-light;
    pointer-events: none;

    a, & > span {
      text-decoration: none;
      color: inherit;
      cursor: default; } } }

/* navigation end */

/* shutter menu switcher */
a.shutter-menu-switcher {
  $btn-menu-size: 60px;
  $btn-menu-lines-width: 35px;
  $btn-menu-lines-height: 5px;
  $btn-menu-lines-space: 8px;
  $btn-menu-lines-color: currentColor;
  $btn-menu-lines-color--mob: currentColor;
  $btn-menu-animation-speed: 0.3s;
  $btn-menu-animation-delay: round-float($btn-menu-animation-speed*0.5, 2);

  $user-options-cross-width: $btn-menu-lines-width;
  $user-options-cross-height: $btn-menu-lines-height;
  $user-options-cross-color: $btn-menu-lines-color;
  $user-options-cross-color--mob: $btn-menu-lines-color--mob;

  // =====================

  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -#{round-float($btn-menu-size / 2, 1)};
  color: $text-color;
  text-decoration: none;
  width: $btn-menu-size;
  height: $btn-menu-size;
  // transition
  $prop: color, background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $btn-menu-animation-speed;
    timing-function: ease-in-out; }

  //      
  .touchevents & {
    user-select: none;
    -webkit-tap-highlight-color: transparent; }

  // 
  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    @include size($user-options-cross-width, $user-options-cross-height);
    background-color: $user-options-cross-color;
    margin: -#{round($user-options-cross-height / 2)} 0 0 -#{round($user-options-cross-width / 2)};
    border-radius: 2px;
    will-change: opacity, transform;
    opacity: 0;
    transition: {
      property: transform, opacity;
      duration: $btn-menu-animation-speed;
      delay: 0s;
      timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

  // 
  & > span {
    @include hide-text; }

  // 
  & > i {
    font-size: 0;
    line-height: 0;
    letter-spacing: -4px;
    position: absolute;
    left: 50%;
    top: 50%;
    @include size($btn-menu-lines-width, $btn-menu-lines-height);
    background-color: $btn-menu-lines-color;
    margin: -#{round($user-options-cross-height / 2)} 0 0 -#{round($user-options-cross-width / 2)};
    border-radius: 2px;
    transition: {
      property: opacity;
      duration: $btn-menu-animation-speed;
      delay: $btn-menu-animation-delay; }

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      right: 0;
      height: $btn-menu-lines-height;
      width: $btn-menu-lines-width + 6;
      background-color: $btn-menu-lines-color;
      border-radius: 2px;
      transition: {
        property: transform;
        duration: $btn-menu-animation-speed;
        delay: $btn-menu-animation-delay; } }

    //first line menu
    &::before {
      top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

    //third line menu
    &::after {
      top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

  & > svg {
    $size: 8rem;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    width: $size;
    height: $size;
    margin-top: -#{round-float($size / 2, 1)};
    margin-left: -#{round-float($size / 2, 1)};
    transform: rotate(-90deg);
    //transform: rotateY(0deg) rotateZ(-90deg)
    stroke-dasharray: 220;
    stroke-dashoffset: 220;
    // transition
    $prop: stroke-dashoffset;
    will-change: $prop;
    transition: {
        property: $prop;
        duration: $btn-menu-animation-speed;
        timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); }

    circle {
      fill: none;
      stroke: currentColor;
      stroke-width: 5;
      stroke-miterlimit: 10; } }

  // hover
  &:hover {
    color: $highlight-color--hover;

    & > i {
      &::before, &::after {
        transition-delay: 0s; }

      &::before {
        transform: translate(3px, 1px) translateZ(0); }

      &::after {
        transform: translate(3px, -1px) translateZ(0); } }

    & > svg {
      stroke-dashoffset: 0;
 } }      //transform: rotateY(180deg) rotateZ(-90deg)

  // opened
  &.shutter-menu_is-open {

    & > svg {
      stroke-dashoffset: 0; }

    &::before, &::after {
      transition-delay: $btn-menu-animation-delay;
      opacity: 1; }

    &::before {
      transform: rotate(45deg) translateZ(0); }

    &::after {
      transform: rotate(-45deg) translateZ(0); }

    & > i {
      opacity: 0;
      transition-delay: 0s;

      &::before, &::after {
        transition-delay: 0s; }

      &::before {
        transform: translate(3px, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

      &::after {
        transform: translate(3px, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } } } }

/* shutter menu switcher end */

/* shutter menu */
.shutter-menu {
  position: fixed;
  right: 0;
  top: 0;
  width: 320px;
  height: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  $translateX: 20%;
  transform: translate($translateX, 0);
  transform: translateX($translateX);
  // transition
  $prop: opacity, visibility, transform;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  &.shutter-menu_is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    $translateX: 0;
    transform: translate($translateX, 0);
    transform: translateX($translateX); } }

/* shutter menu end */

/* overlay */
.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(black, 0.35);
  box-sizing: border-box;
  opacity: 0;
  visibility: hidden;
  $prop: opacity, visibility;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  &.shutter-menu_is-open {
    opacity: 1;
    visibility: visible; } }

/* overlay end */

/* === CONTENT === */
.main {
  flex-shrink: 1;
  flex-grow: 1;
  position: relative;
  //padding-top: 17.6rem
  padding-top: $header-height;
  clear: both;
  z-index: 4;

  .header_float ~ & {
    //padding-top: 6.5rem
    padding-top: 0; } }

/* === FOOTER === */
.footer {
  position: relative;
  font-size: 1.8rem;
  line-height: 2.2rem;
  color: white;
  background-color: #6228f2;
  z-index: 5;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    @include linear-gradient(to bottom, #5b2fc9 0%, #5352ce 100%);
    height: 23.7rem;
    z-index: -1; }

  *::selection {
    background-color: black; } }

.footer-holder {
  position: relative;
  min-height: 63.8rem;
  padding-top: 7rem;
  padding-bottom: 12.9rem;
  @include clear; }

/* footer contacts */
.f-contacts {
  float: right;
  clear: right;
  width: $footer-right-col-width;
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 2rem;
  padding: 0 0 0 nth($footer-grid-space, 2);
  font-size: 2.4rem;
  line-height: 3.2rem;
  list-style: none;

  li {
    margin: 0 4rem 2rem 0;

    &.adr, &.cond {
      display: none; } }

  a {
    color: white;
    text-decoration: none;
    box-shadow: inset 0 -1px 0 rgba(white, 0.5);
    transition: {
      property: color, box-shadow;
      duration: $animation-speed; }

    &[href^="tel:"] {
      box-shadow: none; }

    &:hover {
      color: $highlight-color--hover;
      box-shadow: inset 0 -1px 0 rgba($highlight-color--hover, 0.5);

      &[href^="tel:"] {
        box-shadow: none; } } } }

/* footer contacts end */

/* social networks in footer */
.f-soc {
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  position: absolute;
  left: 0;
  top: 43rem;
  cursor: default;

  @include media('<mob-lg') {
    justify-content: center; }

  &:last-child {
    margin-bottom: 0; } }

.f-soc__group {
  margin-left: 4rem;

  @include media('<desk-lg') {
    margin-left: 1.5rem; }

  @include media('<tab-lg') {
    margin-left: 1rem; }

  &:first-child {
    margin-left: 0; }

  & > strong {
    display: block;
    margin-bottom: 5px;
    @include font-light;
    font-size: 14px;
    line-height: 18px;
    font-weight: normal;
    color: white; } }

.f-soc__list {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 0 -15px -15px 0;

  @include media('<tab-lg') {
    margin: 0 -10px -10px 0; } }

.f-soc__item {
  float: left;
  margin: 0 15px 15px 0;

  a {
    display: block;
    position: relative;
    font-size: 60px;
    @include size(1em);

    @include media('<tab-lg') {
      font-size: 40px; }

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      @include size(100%);
      background-image: url("../../img/soc-sprite.svg");
      background-repeat: no-repeat;
      background-size: 12em auto; } //   , ..   12    , 1em - 1 

    &::after {
      opacity: 0; }

    // hover
    &:hover {
      &::before {
        opacity: 0; }

      &::after {
        opacity: 1; } }

    $soc-list: 'facebook', 'telegram', 'viber';
    $type-length: 4; //      ,        

    @for $i from 1 through length($soc-list) {
      $pos: $type-length - $type-length * $i * 1em; //      background

      //@debug $pos

      &.#{nth($soc-list, $i)} {
        &::before { //       (   -      : 1, 5, 9  ..)
          $p: $pos;
          background-position: $p $p; }

        &::after { //      (   -1em -      : 2, 6, 10  ..)
          $p: $pos - 1;
          background-position: $p $p; }

        .user-form__msg__soc & {
          &::before { //        (   -2em -      : 3, 7, 11  ..)
            $p: $pos - 2;
            background-position: $p $p; }

          &::after { //       (   -3em -      : 4, 8, 12  ..)
            $p: $pos - 3;
            background-position: $p $p; } } } }

    span { //  ,      
      @include hide-text; } } }

/* social networks in footer end */

/* appeal form */
.callback-form {
  float: right;
  clear: right;
  width: $footer-right-col-width;
  position: relative;
  margin-bottom: 5rem;
  padding: 0 0 0 nth($footer-grid-space, 2);

  &:last-child {
    margin-bottom: 0; }

  #{$inputs-list} {
    border-radius: 10px;
    background-color: white; } }

.callback-form__title {
  display: block;
  @include font-bold;
  font-size: 2.4rem;
  line-height: 3.2rem;
  color: white;
  margin-bottom: 3rem; }

.callback-form__fields {
  $space: 3rem 2rem;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 0 -#{round-float(nth($space, 2) / 2, 1)} 1.5rem;

  .input-wrap {
    flex-grow: 1;
    flex-basis: 33.33%;
    width: 33.33%;
    padding: 0 #{round-float(nth($space, 2) / 2, 1)}; } }

// --> form footer <--
.callback-form__footer {
  text-align: right; }

// --> send message <--
.callback-form__msg {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 3rem;
  font-size: 2.4rem;
  line-height: 3.2rem;
  color: white;
  text-align: center;
  background-color: $base-color;
  z-index: 2;
  opacity: 0;
  pointer-events: none;
  // transition
  will-change: opacity;
  transition: opacity $animation-speed ease-in-out;

  .form-success & {
    opacity: 1;
    pointer-events: auto; } }

.callback-form__msg-title {
  display: block;
  margin-bottom: 3rem; }

.callback-form__msg-title,
.callback-form__msg-subtitle {
  max-width: calc(100% - 10rem);
  margin-left: auto;
  margin-right: auto; }

/* appeal form end */

/* footer conditions */
.f-conditions {
  position: absolute;
  top: 30rem;
  left: 0;
  max-width: 30rem;
  font-size: 1.8rem;
  line-height: 2.2rem;

  a {
    padding-top: 0.5em;
    text-decoration: none;
    color: white;
    box-shadow: inset 0 -1px 0 rgba(white, 0.5);
    transition: {
      property: color, box-shadow;
      duration: $animation-speed; }

    &:hover {
      color: $highlight-color--hover;
      box-shadow: inset 0 -1px 0 rgba($highlight-color--hover, 0.5); } } }


/* footer conditions end */

/* footer app */
.f-app {
  position: absolute;
  left: 0;
  top: 8.2rem;
  width: $footer-left-col-width;
  padding: 0 nth($footer-grid-space, 2) 0 0; }

.f-app__label {
  display: none; }

.f-app__item {
  display: block;
  margin-bottom: 4rem; }

/* footer app end */

/* developer */
.developer {
  position: absolute;
  right: 0;
  bottom: 13.5rem;
  text-align: right;
  font-size: 14px;
  line-height: 15px;

  a {
    display: inline-block;
    vertical-align: top;
    color: white;
    text-decoration: none;
    // transition
    transition: color $animation-speed ease-in-out;

    &:hover {
      color: $highlight-color--hover; } } }

.developer__logo {
  display: inline-block;
  vertical-align: top;

  svg {
    display: inline-block;
    vertical-align: top;
    @include size(70px, 15px);
    margin-right: 8px;

    path {
      fill: currentColor; } } }

.developer__name {
  display: inline-block;
  vertical-align: top;
  height: 15px;
  margin-top: -1px; }

.developer-mob {
  display: none; }

/* developer end */

/* lacerta app */
.icon-app {
  $size: 8rem;
  position: fixed;
  right: 3.9rem;
  top: 50%;
  box-shadow: 0 3px 8px rgba(black, 0.11);
  width: $size;
  height: $size;
  margin-top: -#{round-float($size / 2, 1)};
  color: white;
  text-indent: -9999px;
  overflow: hidden;
  background-color: $highlight-color;
  background-image: url('data:image/png;base64,');
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  z-index: $z-middle;
  // transition
  $prop: background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  .no-touchevents &:hover {
    background-color: $highlight-color--hover; }

  &:active {
    margin-top: -#{round-float($size / 2, 1) - 0.1};
    box-shadow: 0 1px 4px rgba(black, 0.11); } }

/* lacerta app end */

/* === MAIN === */
.#{$article-content} {
  padding-bottom: 16rem; }

/* main-caption */
.main-caption {
  margin-bottom: 5rem;
  font-size: 2.4rem;
  line-height: 2.9rem; }

.main-caption__holder {
  position: relative;
  padding: 9.5rem 0 13rem; }

.main-caption__figure {
  position: absolute;
  right: 0;
  top: 0;
  background: url(../../img/main-caption-figure.png) no-repeat;
  width: 758px;
  z-index: -1;

  &::before {
    content: "";
    display: block;
    @include ratio(758, 569); } }

.main-caption__title {
  margin-bottom: 5rem;
  width: 70rem;
  max-width: 100%;

  &:last-child {
    margin-bottom: 0; } }

.main-caption__entry {
  color: $text-color-alt;
  margin-bottom: 2rem;
  width: 70rem;
  max-width: 100%;

  b, strong {
    @include font-bold; }

  i, em {
    @include font-bold-italic; } }

.main-caption__text {
  color: #1a1c26;
  width: 70rem;
  max-width: 100%; }

/* main-caption end */

/* limitations */
.limitations {
  margin-bottom: 5rem;

  &:last-child {
    margin-bottom: 0; } }

.limitations__holder {
  @include clear; }

.limitations__list {
  margin: 0 -2rem;
  @include clear; }

.limitations__item {
  position: relative;
  width: 50%;
  padding: 12rem 2rem 12rem 36rem;
  cursor: default;

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 6.1rem;
    height: 6.1rem;
    left: 36rem;
    top: 4rem;
    background-repeat: no-repeat;
    background-image: url(../../img/limitation-sign.png);
    background-size: 100%;
    opacity: 0;
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    @media #{$retina} {
      background-image: url(../../img/limitation-sign-2x.png); } } }

.limitations__item-figure {
  position: absolute;
  left: 0;
  top: 0;
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: 100%; }

.limitations__item-title {
  @include font-bold;
  font-size: 3.0rem;
  line-height: 1;
  margin-bottom: 2rem;

  &:last-child {
    margin-bottom: 0; } }

.limitations__item-entry {
  font-size: 1.8rem;
  line-height: 2.5rem;
  color: $text-color-alt;
  margin-bottom: 2rem;

  &:last-child {
    margin-bottom: 0; } }

// hover
.limitations__item:hover {
  &::before {
    opacity: 1; } }

// --> bank <--
.limitations__item_deposit {
  float: left;
  clear: both; }

.limitations__item-figure_deposit {
  background-image: url(../../img/limitation-figure-01.png);
  width: 290px;
  height: 259px; }

// hover
.limitations__item_deposit:hover {
  .limitations__item-figure_deposit {
    background-position: 0 -279px; } }

// --> lend <--
.limitations__item_lend {
  float: left;
  clear: left; }

.limitations__item-figure_lend {
  background-image: url(../../img/limitation-figure-02.png);
