    background-image: url(../../img/advantage-sign-2x.png); } }

.profit__title {
  @include font-black;
  font-size: 3.0rem;
  line-height: 1;
  color: white;
  margin-bottom: 2rem;

  &:last-child {
    margin-bottom: 0; } }

.profit__text {
  font-size: 1.8rem;
  line-height: 2.5rem;
  color: white;
  margin-bottom: 2rem;

  &:last-child {
    margin-bottom: 0; } }

/* profit end */

/* profits */
.profits {
  padding-bottom: 7rem; }

.profits__holder {
  position: relative; }

.profits__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 0 -1.5rem -3rem; }

.profits__list-item {
  $width: calc(50% - 3rem);
  flex-grow: 1;
  flex-basis: $width;
  margin: 0 1.5rem 3rem;
  background-repeat: no-repeat;

  &:nth-child(1) {
    background-image: url(../../img/bg-profit-01-desk.png); }

  &:nth-child(2) {
    background-image: url(../../img/bg-profit-02-desk.png); }

  &:nth-child(3) {
    background-image: url(../../img/bg-profit-03-desk.png); }

  &:nth-child(4) {
    background-image: url(../../img/bg-profit-04-desk.png); } }

/* profits end */

/* custom range slider */
.irs--custom {
  height: 116px; }

.irs--custom.irs-with-grid {
  height: 131px; }

.irs--custom .irs-line {
  top: 89px;
  height: 15px;
  background-color: #eaefff;
  border-radius: 15px; }

.irs--custom .irs-bar {
  top: 89px;
  height: 15px;
  background-color: $highlight-color; }

.irs--custom .irs-bar--single {
  border-radius: 12px 0 0 12px; }

.irs--custom .irs-shadow {
  height: 1px;
  bottom: 16px;
  background-color: rgba(66, 139, 202, 0.5); }

.irs--custom .irs-handle {
  top: 71px;
  width: 19px;
  height: 45px;
  background-color: #fff;
  box-shadow: 0 2px 9px rgba(black, 0.24);
  border-radius: 4px; }

.irs--custom .irs-handle.state_hover,
.irs--custom .irs-handle:hover {
  background-color: #fff; }

.irs--custom .irs-min,
.irs--custom .irs-max {
  top: 0;
  padding: 1px 5px;
  color: white;
  text-shadow: none;
  background-color: #9f9f9f;
  border-radius: 3px; }

.irs--custom .irs-from,
.irs--custom .irs-to,
.irs--custom .irs-single {
  color: white;
  text-shadow: none;
  min-width: 131px;
  padding: 10px;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  border-radius: 46px;
  @include linear-gradient(to right, rgba(#4917af, 1) 0%, rgba(#6a35fa, 1) 100%);

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 100%;
    left: 50%;
    margin: 0 -7px;
    @include triangle(bottom, #5a2dcd, 7px, 1.57); } }

.irs--custom .irs-grid-pol {
  background-color: $text-color; }

.irs--custom .irs-grid-text {
  color: $text-color; }

/* custom range slider end */

/* yield */
.yield {
  padding-top: 7rem;
  padding-bottom: 7rem; }

.yield__holder {
  position: relative; }

.yield__title {
  font-size: 5.5rem;
  line-height: 1;
  color: #000;
  margin-bottom: 3rem;

  &:last-child {
    margin-bottom: 0; } }

.yield-box {
  display: flex;
  margin-bottom: 6rem;

  &:last-child {
    margin-bottom: 0; }

  .js &:not(.is-loaded) {
    opacity: 0;
    pointer-events: none;
    overflow: hidden; } }

.yield-box__aside {
  $width: 45.2rem;
  flex-basis: $width;
  flex-shrink: 0;
  margin-right: 6.1rem; }

.yield-thumbs {
  $height: 71.3rem;
  $padding-bottom: 2.2rem;
  $padding-top: 1rem;
  position: relative;
  height: $height + $padding-top + $padding-bottom;
  padding: #{$padding-top} 1.1rem #{$padding-bottom};
  margin: -#{$padding-top} -1.1rem -#{$padding-bottom}; }

.yield-thumbs__item {
  opacity: 0.6;
  height: 22.3rem;
  // transition
  $prop: opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  &.swiper-slide-thumb-active {
    opacity: 1; } }

.yield-box__main {
  flex-grow: 1;
  $width: calc(100% - 45.2rem - 6.1rem);
  flex-basis: $width;
  max-width: $width;
  height: 71.3rem; }

.yield-cards {
  $padding-bottom: 2.2rem;
  $padding-top: 1rem;
  position: relative;
  padding: #{$padding-top} 1.1rem #{$padding-bottom};
  margin: -#{$padding-top} -1.1rem -#{$padding-bottom};

  .swiper-pagination {
    display: none; } }

.yield__footer {
  text-align: right; }
/* yield end */

/* news preview element */
$news-elem-space-outer: 3rem 6.6rem;
$news-elem-space-inner: 1.8rem 3rem;
$news-elem-img-space-h: 3.6rem;
$news-elem-img-space-v: 3rem;
.news-elem {
  background-color: white;
  @include clear; }

.news-elem__figure {
  width: 33.3rem;
  background-repeat: no-repeat;
  background-position: center;
  background-color: $border-color;
  background-size: cover;

  &::before {
    content: "";
    display: block;
    @include ratio(1, 1); } }

.news-elem__content {
  overflow: hidden; }

.news-elem__title {
  position: relative;
  @include font-bold;
  font-size: 2.4rem;
  line-height: 2.9rem;
  color: $title-color;
  margin-bottom: 1.5rem;
  padding-bottom: 1.5rem;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 24rem;
    max-width: 80%;
    border-top: 3px solid $border-color; } }

.news-elem__title {
  font-size: 1.8rem;
  line-height: 2.1rem;
  color: $text-color-alt; }

// type vertical
.type-v {
  .news-elem {
    padding: 0;
    box-shadow: none; }

  .news-elem__figure {
    margin-bottom: $news-elem-img-space-v;
    width: 100%;

    &:last-child {
      margin-bottom: 0; } } }

// type left
.type-l {
  .news-elem {
    box-shadow: inset 0 0 0 1px $border-color; }

  .news-elem__figure {
    float: left; }

  .news-elem__content {
    padding: nth($news-elem-space-inner, 1) nth($news-elem-space-inner, 2) nth($news-elem-space-inner, 1) $news-elem-img-space-h; } }

// type right
.type-r {
  .news-elem {
    box-shadow: inset 0 0 0 1px $border-color; }

  .news-elem__figure {
    float: right; }

  .news-elem__content {
    padding: nth($news-elem-space-inner, 1) $news-elem-img-space-h nth($news-elem-space-inner, 1) nth($news-elem-space-inner, 2); } }

/* news preview element end */

/* news */
.news {
  margin-bottom: 6rem; }

// news list
.news__list {
  margin: 0 -#{round-float(nth($news-elem-space-outer, 2) / 2, 1)} 3rem;
  @include clear; }

.news__list-item {
  padding: 0 #{round-float(nth($news-elem-space-outer, 2) / 2, 1)} nth($news-elem-space-outer, 1);

  // align left
  &.align-l {
    float: left;
    clear: left; }

  // align right
  &.align-r {
    float: right;
    clear: right; }

  &.type-v {
    width: 32.6%; }

  &.type-l, &.type-r {
    width: 67.4%; } }

// news footer
.news__footer {
  text-align: right; }

/* news end */
