@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* forms layout */
.form-row, .input-wrap {
  line-height: 1;
  position: relative;
  // !      
  margin-bottom: $form-bottom-space;
  z-index: 1;
  // !      z-index: -1

  @include media($form-media--mob) {
    margin-bottom: $form-bottom-space--mob; }

  //         
  &:last-child {
    margin-bottom: 0; } }

.input-wrap {
  //.#{$small-field} &
  //  margin-bottom: $form-bottom-space--small

  .form-row & {
    margin-bottom: 0; } }

.form-row:last-child {
  margin-bottom: 0; }

.input-holder {
  position: relative;
  // !      
  z-index: 1; }
// !      z-index: -1

.label-holder, label {
  @include font-normal;
  @include font($form-label-font-size, $form-label-line-height);
  color: inherit;
  margin-bottom: $form-label-vsb;
  // transition
  transition: color $animation-speed ease-in-out;

  .#{$small-field} & {
    @include font($form-label-font-size--small, $form-label-line-height--small);
    margin-bottom: $form-label-vsb--small; }

  .#{$small-field} &, & {
    @include media($form-media--mob) {
      @include font($form-label-font-size--mob, $form-label-line-height--mob);
      margin-bottom: $form-label-vsb--mob; } } }

label, .label {
  @include display-inline-block-top;
  user-select: none;
  -webkit-tap-highlight-color: transparent !important;
  opacity: 0.7;

  .label-holder & {
    margin-bottom: 0; } }

label {
  padding-left: $form-input-space-h;
  @if $form-border-width {
    margin-left: $form-border-width; }

  .#{$small-field} & {
    padding-left: $form-input-space-h--small;
    @if $form-border-width--small {
      margin-left: $form-border-width--small; } }

  .#{$small-field} &, & {
    @include media($form-media--mob) {
      padding-left: $form-input-space-h--mob;
      @if $form-border-width--mob {
        margin-left: $form-border-width--mob; } } } }

.form-mark {
  @include display-inline-block-top;
  position: relative;
  margin-left: -15px;
  left: 15px;
  color: $mark-color;

  .form-note & {
    margin-left: 0;
    left: auto; } }

.form-row {
  @include margin-horizontal(-#{round($form-gap*0.5)});
  font-size: 0;
  letter-spacing: -4px;

  .input-wrap {
    @include display-inline-block-top;
    width: calc(100% / 3 - #{$form-gap});
    @include margin-horizontal(#{round($form-gap*0.5)});
    margin-bottom: 0;

    font-size: $font-size;
    letter-spacing: normal;

    @include media('<tab-lg') {
      display: block;
      width: auto;
      margin-bottom: $form-bottom-space--mob; } }

  label {
    @include truncate(100%); } }

/* forms layout end */

// Input Fields
// input[type="text"]
// input[type="password"]
// input[type="email"]
// input[type="search"]
// textarea

#{$inputs-list} {
  display: block;
  padding: $form-input-space-vt $form-input-space-h $form-input-space-vb;
  width: 100%;
  height: $form-input-height;
  background-color: $form-bg-color;
  border-radius: round($form-input-height/2);
  box-shadow: none;
  @if $form-border-width {
    border: $form-border-width solid $form-border-color; }
  cursor: pointer;
  // transition
  $prop: color, border-color, background-color;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  .#{$small-field} & {
    padding: $form-input-space-vt--small $form-input-space-h--small $form-input-space-vb--small;
    height: $form-input-height--small;
    border-radius: round($form-input-height--small/2);
    @if $form-border-width--small {
      border-width: $form-border-width--small; } }

  .#{$small-field} &, & {
    @include media($form-media--mob) {
      padding: $form-input-space-vt--mob $form-input-space-h--mob $form-input-space-vb--mob;
      height: $form-input-height--mob;
      border-radius: round($form-input-height--mob/2);
      @if $form-border-width--mob {
        border-width: $form-border-width--mob; } } }

  //         ,
  //          
  & {
    @include inputs-font;

    .#{$small-field} & {
      @include inputs-font--small; }

    .#{$small-field} &, & {
      @include media($form-media--mob) {
        @include inputs-font--mob; } } }

  //          ,
  //          
  & {
    @include forms-space;

    .#{$small-field} & {
      @include forms-space--small; } }

  //    ,     
  //   ,      
  .input-wrap &, &:last-child {
    margin-bottom: 0; } }

/* Change autocomplete styles in WebKit */
//&:-webkit-autofill,
//&:-webkit-autofill:hover,
//&:-webkit-autofill:focus
//  -webkit-text-fill-color: $form-text-color
//  -webkit-box-shadow: 0 0 0 1000px $form-bg-color inset

textarea {
  $lines: 5;
  $height: round($form-line-height*$lines) + $form-input-space-vt + $form-input-space-vb + round($form-border-width*2);
  min-height: $height;
  height: $height;
  resize: vertical;

  .#{$small-field} & {
    //$height: round($form-line-height--small*$lines) + $form-input-space-vt--small + $form-input-space-vb--small + round($form-border-width--small*2)
    $height: 137px;
    min-height: $height;
    height: $height; }

  .#{$small-field} &, & {
    @include media($form-media--mob) {
      $height: round($form-line-height--mob*$lines) + $form-input-space-vt--mob + $form-input-space-vb--mob + round($form-border-width--mob*2);
      min-height: $height;
      height: $height; } }

  &[disabled] {
    resize: none; } }

// Placeholder
input, textarea {
  @include placeholder {
    color: $form-placeholder-color;
    transition: $animation-speed opacity; }

  //form placeholder hide on focus
  @include placeholder-focus {
    opacity: 0; } }

// States
#{$inputs-list} {
  &[disabled] {
    cursor: default;
    color: $form-text-color--disabled;
    background-color: $form-bg-color--disabled;
    border-color: $form-border-color--disabled;

    @include placeholder {
      color: $form-text-color--disabled; } }

  &:not([disabled]):not(.error):not(.success):not(.filled) {
    //form hover
    //&:hover
    //border-color: $form-border-color--focus

    //form focus and filled
    &:focus, &.focus {
      @include form-has-value; } }

  &:focus, &.focus {
    outline: none; }

  // has value (js)
  //style for label
  @at-root label {
    &.filled {
      @include form-has-value; } }

  &.filled:not([disabled]) {
    @include form-has-value; }

  // on focus (js)
  //&.input--focus
  //  @include form-on-focus

  //form success
  //style for label
  @at-root label {
    &.success {
      color: $form-success-color;
      @include form-success; } }

  &.success:not([disabled]) {
    @include form-success; }

  //form success placeholder
  //+placeholder
  //  color: $form-success-color

  //form error
  @at-root label {
    &.error {
      @include form-error; } }

  &.error:not([disabled]) {
    @include form-error; } }

//form error placeholder
//+placeholder
//  color: $form-error-color

/* inputs states end */

@import "checkboxes";
@import "selects";
@import "select2";
@import "validation";
