  left: 4.6rem;
  margin-top: -6.6rem;
  background-image: url(../img/profit-sign.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

@media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi) {
  .profit__icon {
    background-image: url(../img/advantage-sign-2x.png);
  }
}

.profit__title {
  font-family: "proximanova-black", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 3.0rem;
  line-height: 1;
  color: white;
  margin-bottom: 2rem;
}

.profit__title:last-child {
  margin-bottom: 0;
}

.profit__text {
  font-size: 1.8rem;
  line-height: 2.5rem;
  color: white;
  margin-bottom: 2rem;
}

.profit__text:last-child {
  margin-bottom: 0;
}

/* profit end */
/* profits */
.profits {
  padding-bottom: 7rem;
}

.profits__holder {
  position: relative;
}

.profits__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 0 -1.5rem -3rem;
}

.profits__list-item {
  flex-grow: 1;
  flex-basis: calc(50% - 3rem);
  margin: 0 1.5rem 3rem;
  background-repeat: no-repeat;
}

.profits__list-item:nth-child(1) {
  background-image: url(../img/bg-profit-01-desk.png);
}

.profits__list-item:nth-child(2) {
  background-image: url(../img/bg-profit-02-desk.png);
}

.profits__list-item:nth-child(3) {
  background-image: url(../img/bg-profit-03-desk.png);
}

.profits__list-item:nth-child(4) {
  background-image: url(../img/bg-profit-04-desk.png);
}

/* profits end */
/* custom range slider */
.irs--custom {
  height: 116px;
}

.irs--custom.irs-with-grid {
  height: 131px;
}

.irs--custom .irs-line {
  top: 89px;
  height: 15px;
  background-color: #eaefff;
  border-radius: 15px;
}

.irs--custom .irs-bar {
  top: 89px;
  height: 15px;
  background-color: #28e4cc;
}

.irs--custom .irs-bar--single {
  border-radius: 12px 0 0 12px;
}

.irs--custom .irs-shadow {
  height: 1px;
  bottom: 16px;
  background-color: rgba(66, 139, 202, 0.5);
}

.irs--custom .irs-handle {
  top: 71px;
  width: 19px;
  height: 45px;
  background-color: #fff;
  box-shadow: 0 2px 9px rgba(0, 0, 0, 0.24);
  border-radius: 4px;
}

.irs--custom .irs-handle.state_hover,
.irs--custom .irs-handle:hover {
  background-color: #fff;
}

.irs--custom .irs-min,
.irs--custom .irs-max {
  top: 0;
  padding: 1px 5px;
  color: white;
  text-shadow: none;
  background-color: #9f9f9f;
  border-radius: 3px;
}

.irs--custom .irs-from,
.irs--custom .irs-to,
.irs--custom .irs-single {
  color: white;
  text-shadow: none;
  min-width: 131px;
  padding: 10px;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  border-radius: 46px;
  background: #4917af;
  background: -webkit-linear-gradient(left, #4917af 0%, #6a35fa 100%);
  background: linear-gradient(to right, #4917af 0%, #6a35fa 100%);
}

.irs--custom .irs-from::after,
.irs--custom .irs-to::after,
.irs--custom .irs-single::after {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  left: 50%;
  margin: 0 -7px;
  width: 0;
  height: 0;
  z-index: 2;
  border-top: 11px solid #5a2dcd;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
}

.irs--custom .irs-grid-pol {
  background-color: #1e202b;
}

.irs--custom .irs-grid-text {
  color: #1e202b;
}

/* custom range slider end */
/* yield */
.yield {
  padding-top: 7rem;
  padding-bottom: 7rem;
}

.yield__holder {
  position: relative;
}

.yield__title {
  font-size: 5.5rem;
  line-height: 1;
  color: #000;
  margin-bottom: 3rem;
}

.yield__title:last-child {
  margin-bottom: 0;
}

.yield-box {
  display: flex;
  margin-bottom: 6rem;
}

.yield-box:last-child {
  margin-bottom: 0;
}

.js .yield-box:not(.is-loaded) {
  opacity: 0;
  pointer-events: none;
  overflow: hidden;
}

.yield-box__aside {
  flex-basis: 45.2rem;
  flex-shrink: 0;
  margin-right: 6.1rem;
}

.yield-thumbs {
  position: relative;
  height: 74.5rem;
  padding: 1rem 1.1rem 2.2rem;
  margin: -1rem -1.1rem -2.2rem;
}

.yield-thumbs__item {
  opacity: 0.6;
  height: 22.3rem;
  will-change: opacity;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.yield-thumbs__item.swiper-slide-thumb-active {
  opacity: 1;
}

.yield-box__main {
  flex-grow: 1;
  flex-basis: calc(100% - 45.2rem - 6.1rem);
  max-width: calc(100% - 45.2rem - 6.1rem);
  height: 71.3rem;
}

.yield-cards {
  position: relative;
  padding: 1rem 1.1rem 2.2rem;
  margin: -1rem -1.1rem -2.2rem;
}

.yield-cards .swiper-pagination {
  display: none;
}

.yield__footer {
  text-align: right;
}

/* yield end */
/* news preview element */
.news-elem {
  background-color: white;
}

.news-elem::after {
  content: '';
  display: table;
  clear: both;
}

.news-elem__figure {
  width: 33.3rem;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #eaefff;
  background-size: cover;
}

.news-elem__figure::before {
  content: "";
  display: block;
  height: 0;
  padding-top: 100%;
}

.news-elem__content {
  overflow: hidden;
}

.news-elem__title {
  position: relative;
  font-family: "proximanova-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 2.4rem;
  line-height: 2.9rem;
  color: #000;
  margin-bottom: 1.5rem;
  padding-bottom: 1.5rem;
}

.news-elem__title::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 24rem;
  max-width: 80%;
  border-top: 3px solid #eaefff;
}

.news-elem__title {
  font-size: 1.8rem;
  line-height: 2.1rem;
  color: #40455d;
}

.type-v .news-elem {
  padding: 0;
  box-shadow: none;
}

.type-v .news-elem__figure {
  margin-bottom: 3rem;
  width: 100%;
}

.type-v .news-elem__figure:last-child {
  margin-bottom: 0;
}

.type-l .news-elem {
  box-shadow: inset 0 0 0 1px #eaefff;
}

.type-l .news-elem__figure {
  float: left;
}

.type-l .news-elem__content {
  padding: 1.8rem 3rem 1.8rem 3.6rem;
}

.type-r .news-elem {
  box-shadow: inset 0 0 0 1px #eaefff;
}

.type-r .news-elem__figure {
  float: right;
}

.type-r .news-elem__content {
  padding: 1.8rem 3.6rem 1.8rem 3rem;
}

/* news preview element end */
/* news */
.news {
  margin-bottom: 6rem;
}

.news__list {
  margin: 0 -3.3rem 3rem;
}

.news__list::after {
  content: '';
  display: table;
  clear: both;
}

.news__list-item {
  padding: 0 3.3rem 3rem;
}

.news__list-item.align-l {
  float: left;
  clear: left;
}

.news__list-item.align-r {
  float: right;
  clear: right;
}

.news__list-item.type-v {
  width: 32.6%;
}

.news__list-item.type-l, .news__list-item.type-r {
  width: 67.4%;
}

.news__footer {
  text-align: right;
}

/* news end */
/*======================================================*/
/* PRINT VERSION */
/*======================================================*/
@media print {
  *,
  *::before,
  *::after {
    background: transparent !important;
    color: black !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  a, a:visited {
    text-decoration: underline;
  }
  a[href^=http]::after {
    content: " <" attr(href) "> ";
  }
  a[href]::after {
    content: " (" attr(href) ")";
  }
  abbr[title]::after {
    content: " (" attr(title) ")";
  }
  a[href^="#"]::after, a[href^="javascript:"]::after {
    content: "";
  }
  pre, blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  tr, img {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  p, h2, h3 {
    orphans: 3;
    widows: 3;
  }
  h2, h3 {
    page-break-after: avoid;
  }
  hr {
    border-color: black;
  }
  body {
    font-family: Arial, Helvetica, sans-serif !important;
    font-size: 10pt !important;
    line-height: 1.5 !important;
    min-width: 1024px !important;
  }
  h1, h2, h3, h4, h5, h6,
  b, strong, th, i, em,
  blockquote, q, cite,
  table td, table th, table {
    font-family: Arial, Helvetica, sans-serif !important;
    font-weight: normal !important;
    font-style: normal !important;
  }
  h1 {
    font-size: 20pt !important;
    line-height: 1.3 !important;
  }
  h2 {
    font-size: 18pt !important;
    line-height: 1.4 !important;
  }
  h3, .main-slider-title__item {
    font-size: 16pt !important;
    line-height: 1.5 !important;
  }
  h4, blockquote q {
    font-size: 14pt !important;
    line-height: 1.5 !important;
  }
  h5, blockquote cite {
    font-size: 12pt !important;
    line-height: 1.6 !important;
  }
  h6 {
    font-size: 8pt !important;
    line-height: 1.8 !important;
  }
  b, strong, th {
    font-weight: bold !important;
  }
  i, em {
    font-style: italic !important;
  }
  i b, i strong, em b, em strong,
  b i, b em, strong i, strong em {
    font-weight: bold !important;
    font-style: italic !important;
  }
  table td, table th, table {
    border: 1px solid black !important;
    font-size: 9pt !important;
    color: black !important;
    padding: 3pt !important;
  }
  table td > *, table th > *, table > * {
    padding: 0 !important;
  }
  .sidebar, .aside, .footer, .spacer,
  .print-hidden, .view-print,
  .nav, .nav-list,
  .breadcrumbs, .breadcrumbs__list,
  [class*="btn-"],
  form,
  .flashes,
  .images-slider__item::before,
  .images-slider__list .slick-arrow,
  .images-slider__list .slick-dots,
  .images-slider__list .slider-counter,
  .images-slider__item img,
  .class {
    display: none !important;
  }
  .images-slider__list,
  .images-slider__list .slick-list,
  .images-slider__list .slick-track,
  .images-slider__item,
  .images-slider__item .caption,
  .class {
    display: block !important;
    float: none !important;
    position: relative !important;
    overflow: visible !important;
    height: auto !important;
    min-height: 0 !important;
    max-height: none !important;
    width: auto !important;
    min-width: 0 !important;
    max-width: none !important;
    left: auto !important;
    top: auto !important;
    right: auto !important;
    bottom: auto !important;
    padding: 0 !important;
    margin: 0 !important;
    transform: none !important;
    text-align: left !important;
    border: none !important;
    box-shadow: none !important;
    opacity: 1 !important;
    visibility: visible !important;
    z-index: auto !important;
  }
  .images-slider__item,
  .class {
    margin-top: 0 !important;
    margin-bottom: 0.5cm !important;
    padding-bottom: 0 !important;
  }
  .header, .blockquote, hr, blockquote,
  .class {
    margin-top: 0 !important;
    margin-bottom: 1cm !important;
    padding-bottom: 0 !important;
  }
  .logo {
    background: none !important;
  }
  .logo > * {
    display: none !important;
  }
  .logo > img {
    display: block !important;
    float: left !important;
  }
  blockquote q::before,
  blockquote q::after {
    padding: 0 !important;
    margin: 0 !important;
    font-size: 18pt !important;
    display: inline-block !important;
  }
  table {
    width: 100% !important;
  }
  .article-content img {
    max-width: 8cm !important;
  }
}
